
.movies{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap:wrap;
    padding:50px;
    width: 60%;


}

.movies .movie{
    width:45%;
    background-color: white;
    margin-bottom: 70px;
    display:flex;
    justify-content: flex-start;
    font-weight: 300;
    padding:20px;
    padding-top: 70px;
    position: relative;
    color: #515264;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025);


}
.movie img{
    position: relative;
    top: -50px;
    width: 100%;
    max-width: 180px;
    margin-right:30px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie .movie_genres,
.movie .movie_year
{
    margin: 0;
    font-weight: 300;
}

.movie h4{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres
{

    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;

    margin:5px 0px;
}
.movie_genres li{
    margin-right: 10px;
    font-size: 14px;
}